<script>
    import { onMount } from 'svelte';

    import * as THREE from 'three';
    import { RoomEnvironment } from 'three/examples/jsm/environments/RoomEnvironment.js';
    import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';
    import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';

    // import { KTX2Loader } from 'three/examples/jsm/loaders/KTX2Loader.js';
    import { MeshoptDecoder } from 'three/examples/jsm/libs/meshopt_decoder.module.js';

    let rendererElement;
    let object,
        children = [],
        selected = [];
    let camera, scene, controls, renderer;

    function init() {
        renderer = new THREE.WebGLRenderer({ antialias: true });
        renderer.setPixelRatio(window.devicePixelRatio);
        renderer.setSize(window.innerWidth, window.innerHeight);
        renderer.toneMapping = THREE.ACESFilmicToneMapping;
        renderer.toneMappingExposure = 1;
        renderer.outputEncoding = THREE.sRGBEncoding;
        rendererElement.appendChild(renderer.domElement);

        camera = new THREE.PerspectiveCamera(45, window.innerWidth / window.innerHeight, 1, 2000);
        camera.position.set(50, 100, 0);

        const environment = new RoomEnvironment();
        const pmremGenerator = new THREE.PMREMGenerator(renderer);

        scene = new THREE.Scene();
        scene.background = new THREE.Color(0xbbbbbb);
        scene.environment = pmremGenerator.fromScene(environment).texture;

        const grid = new THREE.GridHelper(500, 10, 0xffffff, 0xffffff);
        grid.material.opacity = 0.5;
        grid.material.depthWrite = false;
        grid.material.transparent = true;
        scene.add(grid);

        const axesHelper = new THREE.AxesHelper(5);
        scene.add(axesHelper);

        // const ktx2Loader = new KTX2Loader().setTranscoderPath('js/libs/basis/').detectSupport(renderer);

        const loader = new GLTFLoader().setPath('models/');
        // loader.setKTX2Loader(ktx2Loader);
        loader.setMeshoptDecoder(MeshoptDecoder);
        loader.load('table.glb', function (gltf) {
            console.log('gltf', gltf);

            object = gltf.scene;

            gltf.scene.scale.set(50, 50, 50);

            object.traverse(function (child) {
                if (child instanceof THREE.Mesh) {
                    children = [...children, child];
                }
            });

            scene.add(gltf.scene);

            render();
        });

        controls = new OrbitControls(camera, renderer.domElement);
        controls.addEventListener('change', render); // use if there is no animation loop
        controls.minDistance = 400;
        controls.maxDistance = 1000;
        controls.target.set(10, 90, -40);
        controls.update();

        window.addEventListener('resize', onWindowResize);
    }

    function onWindowResize() {
        camera.aspect = window.innerWidth / window.innerHeight;
        camera.updateProjectionMatrix();

        renderer.setSize(window.innerWidth, window.innerHeight);

        render();
    }

    //

    function render() {
        renderer.render(scene, camera);
    }

    // init

    onMount(() => {
        init();
        render();
    });

    $: console.log('selected', selected);
</script>

<div class="container my-5">
    <div class="ratio ratio-16x9">
        <div class="w-100 h-100">
            <div class="card w-100 h-100 rounded-0 ">
                <div class="card-header d-flex align-items-center justify-content-between rounded-0">
                    <div class="group">
                        {#if children?.length}
                            {#each children as { uuid, name }, i}
                                <div class="form-check form-check-inline">
                                    <input
                                        class="form-check-input rounded-0"
                                        type="checkbox"
                                        id="inlineCheckbox-{name}"
                                        value={name}
                                        on:change={({ target: { checked } }) => {
                                            if (checked) {
                                                const index = selected.findIndex((x) => x === uuid);
                                                if (index === -1) selected = [...selected, children[i]?.uuid];
                                                // children[i].visible = true;
                                            } else {
                                                selected = selected.filter((x) => x !== uuid);
                                                // children[i].visible = false;
                                            }
                                            render();
                                        }}
                                    />
                                    <label class="form-check-label" for="inlineCheckbox-{name}">{name}</label>
                                </div>
                            {/each}
                        {/if}
                    </div>
                    <div class="group">
                        <input
                            type="color"
                            class="form-control form-control-color rounded-0"
                            id="colorInput"
                            value="#563d7c"
                            disabled={!selected?.length}
                            title="Choose your color"
                            on:change={({ target: { value } }) => {
                                object.traverse(function (child) {
                                    if (selected.includes(child.uuid)) {
                                        const material = child.material.clone();
                                        material.color.set(value);
                                        child.material = material;
                                        setTimeout(() => {
                                            render();
                                        });
                                    }
                                });
                            }}
                        />
                    </div>
                </div>
                <div class="card-header">
                    <div class="input-group">
                        <label for="formFile" class="input-group-text rounded-0">Change material</label>
                        <input
                            class="form-control rounded-0"
                            type="file"
                            id="formFile"
                            disabled={!selected?.length}
                            on:change={(e) => {
                                let file = e.target.files[0];

                                let reader = new FileReader();

                                reader.readAsDataURL(file);

                                reader.onload = function () {
                                    console.log('first');
                                    object.traverse(function (child) {
                                        if (selected.includes(child.uuid)) {
                                            const material = child.material.clone();
                                            material.map = THREE.ImageUtils.loadTexture(reader.result);
                                            material.needsUpdate = true;
                                            child.material = material;
                                            setTimeout(() => {
                                                render();
                                            });
                                        }
                                    });
                                };

                                reader.onerror = function () {
                                    console.log(reader.error);
                                };
                            }}
                        />
                    </div>
                </div>
                <div class="w-100 h-100 position-relative">
                    <div id="renderer" bind:this={rendererElement} />
                </div>
            </div>
        </div>
    </div>
</div>

<style lang="scss">
    #renderer {
        overflow: hidden;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
    :global(canvas) {
        width: 100%;
        height: 100%;
        max-width: 100%;
        max-height: 100%;
        display: block;
    }
</style>
